@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Roboto:wght@400;700&display=swap');

body {
  background-color: #1a1a1a;
  color: #f0f0f0;
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #0d0d0d;
  padding: 2rem 1rem;
  border-bottom: 4px solid #4a4a4a;
}

.App-header h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 3rem;
  color: #e6e6e6;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}

.dinosaur-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.dinosaur-card {
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid #4a4a4a;
  border-radius: 10px;
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.dinosaur-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0,0,0,0.5);
  border-color: #c5a86a;
}

.dinosaur-card h3 {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.5rem;
  margin-top: 0;
  color: #e6e6e6;
}

.dinosaur-card img {
  max-width: 100%;
  border-radius: 5px;
}

.dinosaur-detail-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.dinosaur-detail {
  background-color: #1a1a1a;
  padding: 2rem;
  border-radius: 10px;
  max-width: 600px;
  width: 90%;
  border: 1px solid #4a4a4a;
  box-shadow: 0 8px 16px rgba(0,0,0,0.5);
  position: relative;
}

.dinosaur-detail h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2.5rem;
  color: #c5a86a;
  margin-top: 0;
}

.dinosaur-detail p {
  font-size: 1.1rem;
  line-height: 1.6;
}

.dinosaur-detail strong {
  color: #c5a86a;
}

.close-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  color: #f0f0f0;
  font-size: 2rem;
  cursor: pointer;
  line-height: 1;
}

.close-button:hover {
  color: #c5a86a;
}
